import React from "react";
import PropTypes from "prop-types";
import { StaticImage, GatsbyImage, getImage } from "gatsby-plugin-image";
import Skeleton from "react-loading-skeleton";

/**
 * Settings
 */
const skeletonDuration = 5;

/**
 * View
 * @param {object} props
 */
function ArticlePreview({ title, cover, excerpt, link, skeletonMode }) {
  // card image
  const CardCover = () => {
    const CardCoverAttrs = {
      className: "card-img-top image-cover ratio-cinema",
      alt: "",
    };
    if (skeletonMode)
      return (
        <StaticImage
          src="../assets/images/default_soon.png"
          {...CardCoverAttrs}
        />
      );
    if (!cover)
      return (
        <StaticImage src="../assets/images/default.png" {...CardCoverAttrs} />
      );
    return <GatsbyImage image={getImage(cover)} {...CardCoverAttrs} />;
  };

  // card title
  const CardTitle = () => {
    if (!title && !skeletonMode) return <></>;
    return (
      <h3 className="h5 card-title">
        {skeletonMode ? (
          <Skeleton width="8em" height="1em" duration={skeletonDuration} />
        ) : (
          title
        )}
      </h3>
    );
  };

  // card body
  const CardText = () => {
    if (!excerpt && !skeletonMode) return <></>;
    return (
      <div className="card-text">
        {skeletonMode ? (
          <Skeleton count={5} duration={skeletonDuration} />
        ) : (
          excerpt
        )}
      </div>
    );
  };

  // card link
  const CardLink = () => {
    if (!link && !skeletonMode) return <></>;
    return (
      <>
        {skeletonMode ? (
          <Skeleton width="6em" height="1em" duration={skeletonDuration} />
        ) : (
          <a href={link} className="stretched-link text-muted">
            Lire l'article
          </a>
        )}
      </>
    );
  };

  // render
  return (
    <div className="card h-100">
      <CardCover />
      <div className="card-body">
        <CardTitle />
        <CardText />
      </div>
      <div className="card-footer text-center">
        <CardLink />
      </div>
    </div>
  );
}

ArticlePreview.propTypes = {
  cover: PropTypes.object,
  title: PropTypes.string,
  excerpt: PropTypes.string,
  link: PropTypes.string,
  skeletonMode: PropTypes.bool,
};

ArticlePreview.defaultProps = {
  cover: null,
  title: null,
  excerpt: null,
  link: null,
  skeletonMode: false,
};

export default ArticlePreview;
