/**
 * Format data from graphQL into a friendly flat object
 * @param {object} data from graphQL
 */
export function formatBlogData(data) {
  return {
    title: data?.frontmatter?.title || null,
    date: data?.frontmatter?.date || null,
    category: data?.frontmatter?.category || null,
    cover: data?.frontmatter?.cover?.childImageSharp || null,
    excerpt: data.excerpt || null,
    link: data?.fields?.slug || null,
    body: data.body || null,
  };
}
