import React from "react";
import ArticlePreview from "./articlePreview";
import { formatBlogData } from "../services/formaters";

const minPostCount = 3;

function PostsList({ posts }) {
  return (
    <div className="row">
      {posts.map((post, index) => (
        <div className="col-12 col-md-6 col-lg-4 pb-4" key={index}>
          <ArticlePreview {...formatBlogData(post)} />
        </div>
      ))}
      {/*
        display fake content to fill the space and tease future publications
        but hide when necessary to keep a good UX on mobile
      */}
      {posts.length + 1 <= minPostCount && (
        <div className="col-12 col-md-6 col-lg-4 pb-4 d-none d-md-block">
          <ArticlePreview skeletonMode />
        </div>
      )}
      {posts.length + 2 <= minPostCount && (
        <div className="col-12 col-md-6 col-lg-4 pb-4 d-none d-lg-block">
          <ArticlePreview skeletonMode />
        </div>
      )}
    </div>
  );
}

export default PostsList;
